/** @jsx jsx */
import React from 'react'
import { jsx } from 'theme-ui'
import { Link } from 'gatsby'

const styles = {}

export default ({ children, href, to, ...props }) => {
  const isExternal = href.startsWith('http') // covers http and https
  const target = to || href
  console.log(
    `target: ${target} isExternal: ${isExternal} to: ${to} href: ${href}`
  )
  return isExternal ? (
    <a {...props} href={href} sx={styles}>
      {children}
    </a>
  ) : (
    <Link {...props} to={target} activeClassName='active' sx={styles}>
      {children}
    </Link>
  )
}
